@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  padding: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.__react_component_tooltip {
  background-color: #1F2937 !important; /* Tailwind's gray-800 */
  color: #FFFFFF !important; /* White text */
  font-size: 0.875rem !important; /* Tailwind's text-sm */
  padding: 0.5rem 0.75rem !important; /* Tailwind's p-2 */
  border-radius: 0.25rem !important; /* Tailwind's rounded */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1) !important; /* Tailwind's shadow-md */
}
/* index.css or styles.css */

/* Slider Track */
input[type='range'].slider {
  -webkit-appearance: none;
  width: 100%;
  background: transparent; /* Remove default track styling */
}

input[type='range'].slider:focus {
  outline: none;
}

/* WebKit */
input[type='range'].slider::-webkit-slider-runnable-track {
  height: 8px;
  background: #e5e7eb; /* Tailwind's gray-200 */
  border-radius: 4px;
}

input[type='range'].slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  margin-top: -6px; /* Center the thumb on the track */
  width: 20px;
  height: 20px;
  background: #3b82f6; /* Tailwind's blue-500 */
  border-radius: 50%;
  cursor: pointer;
  transition: background 0.3s ease;
}

input[type='range'].slider::-webkit-slider-thumb:hover {
  background: #2563eb; /* Tailwind's blue-600 */
}

/* Mozilla Firefox */
input[type='range'].slider::-moz-range-track {
  height: 8px;
  background: #e5e7eb;
  border-radius: 4px;
}

input[type='range'].slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #3b82f6;
  border-radius: 50%;
  cursor: pointer;
  transition: background 0.3s ease;
}

input[type='range'].slider::-moz-range-thumb:hover {
  background: #2563eb;
}

/* Microsoft Edge and IE */
input[type='range'].slider::-ms-track {
  height: 8px;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type='range'].slider::-ms-fill-lower {
  background: #e5e7eb;
  border-radius: 4px;
}

input[type='range'].slider::-ms-fill-upper {
  background: #e5e7eb;
  border-radius: 4px;
}

input[type='range'].slider::-ms-thumb {
  width: 20px;
  height: 20px;
  background: #3b82f6;
  border-radius: 50%;
  cursor: pointer;
  transition: background 0.3s ease;
}

input[type='range'].slider:hover::-ms-thumb {
  background: #2563eb;
}
